@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user options */
$uo-space: 34px;
$uo-icon-size: 32px;

.user-options {
	position: relative;
	list-style: none;
	padding: 0;
	margin: 0 -#{round($uo-space/2)};
	@include clear-spacing;
	line-height: 1;

	.header & {
		position: absolute;
		right: 0;
		top: 0; } }

.user-options__item {
	position: relative;
	@include display-inline-block-middle;
	@include font($font-size, 1);
	letter-spacing: normal;

	& > a {
		position: relative;
		display: block;
		color: $text-color;
		line-height: 1;
		//  svg
		fill: $text-color;
		text-decoration: none;
		text-align: center;

		&::before {
			content: "";
			@include display-inline-block-middle;
			height: 100%; }

		svg {
			@include size($uo-icon-size);

			& ~ span {
				padding-left: 10px; }

			& ~ span.hide-text {
				padding-left: 0; } }

		path {
			fill: inherit;
			transition: {
				property: fill;
				duration: $animation-speed; } }

		& > span {
			@include display-inline-block-middle; }

		// hover
		&:hover {
			color: $link-color;
			fill: $link-color; }

		&.active {
			.for-close {
				opacity: 1;
				visibility: visible;
				z-index: 1; }

			.for-open {
				opacity: 0;
				visibility: hidden;
				z-index: auto; } } } }

// search link
.search-link {
	.for-close {
		@include size(12px);
		margin: -6px 0 0 -6px; } }

// basket link
.user-options-counter {
	$size: 18px;
	$border: 2px;
	position: absolute;
	left: 0;
	top: 50%;
	margin: -18px 0 0 24px;
	padding: 0 3px;
	height: $size+$border*2;
	min-width: $size+$border*2;
	border-radius: round(($size+$border*2)/2);
	background-color: $base-color;
	border: $border solid $bg-color-base;
	@include font(11px, $size);
	color: $bg-color-base;
	text-align: center; }

// lang link
.lang-link {
	display: none;

	@include media('<desk-sm') {
		display: inline-block; } }

