@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* FOOTER */
.footer {
	@include font(16px, round-float(24/16, 3));
	color: $text-color;
	border-top: 1px solid $border-color;

	background-color: lightgreen; }

.footer__holder {
	overflow: hidden;
	cursor: default;
	padding: $footer-grid-space 0; }

/* footer bottom */
.footer-bottom {
	color: $text-color; }

.footer-bottom__holder {
	position: relative;
	text-align: justify;
	overflow: hidden;
	cursor: default;

	@include clear-spacing;

	&::after {
		content: "1";
		display: inline-block;
		margin-left: 100%;
		height: 1px;
		overflow: hidden; } }

.footer-bottom__col {
	@include display-inline-block-top;
	@include font(16px, round-float(24/16, 3));
	letter-spacing: normal;

	&:last-child {
		text-align: right; }

	a {
		color: $text-color;
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: underline; } } }

/* footer bottom end */

/* developer */
.developer {
	display: inline-block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px);

		@if $animation-use {
			transition: {
				property: opacity, transform, filter;
				duration: 0.2s; } } }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px;

		//.no-touchevents a:hover
		//ransform: translate(0, 1px)
		//ilter: blur(2px)
 }		//pacity: 0.6

	&::after {
		@include pseudo();
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

/* developer end */
/* FOOTER end */
