@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*scroll sections */
.main-section-js {
	position: relative; }

.main-section__container {
	height: 100%; }

.main-section__align {
	height: 100%;
	margin: 0 auto; }

.main-section--main {
	display: table;
	table-layout: fixed;
	width: 100%;
	text-align: center;

	.main-section__container {
		display: table-cell;
		vertical-align: middle; }

	.main-section__align {
		height: auto; }

	h1, .main-section__title {
		@include font-title;
		@include font(100px, 1);
		text-transform: uppercase;
		color: white; } }


/*scroll sections end */

