@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* slick sliders */
// arrows
.slick-arrow {
	@include size(40px);
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;

	.no-touchevents &:hover {
		opacity: 1;
		background: #e6e6e6; }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size(12px);
		transform: rotate(45deg);
		border: 0 solid #000; } }

.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 2px 2px;
		margin: -6px 0 0 -4px; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 2px 2px 0 0;
		margin: -6px -4px 0 0; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .#{$user-content} & {
			padding: 4px 10px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 12px;
			height: 12px;
			padding: 0;
			border-radius: 50%;
			background: $base-color;
			position: relative;
			transition-property: background, border, box-shadow, transform;
			transition-duration: 0.6s;

			&::before {
				content: '';
				position: absolute;
				left: 2px;
				top: 2px;
				height: 8px;
				width: 8px;
				background: #fff;
				border-radius: 50%;
				opacity: 1;
				box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20);
				transition-property: transform, opacity;
				transition-duration: $animation-speed; } }

		&.slick-active button::before {
			opacity: 0; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */
